@import url(https://fonts.googleapis.com/css?family=Lato:300,400,700);
@import url(https://fonts.googleapis.com/css?family=Anton);
* {
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}

body {
    font-family: 'Lato', 'Helvetica Neue', sans-serif;
    background-color: #f4f4f7;
}

h1 {
    font-family: 'Anton', sans-serif;
}

a.link-unstyled {
    color: inherit;
}

a.link-unstyled:hover {
    color: inherit;
    text-decoration: none;
}

.btn {
    padding: 0.375rem 2rem;
}

.btn.btn-primary {
    color: #fff;
    background-color: #ef4923;
    border-color: #ef4923;
}

.btn.btn-primary:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #992207;
    border-color: #992207;
}

.btn.btn-primary:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #992207;
    border-color: #992207;
}

.btn.btn-primary:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem #ee684a;
}

.btn.btn-danger {
    color: #fff;
    background-color: #cd4040;
    border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):hover {
    color: #fff;
    background-color: #cd4040;
    border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):active {
    color: #fff;
    background-color: #cd4040;
    border-color: #cd4040;
}

.btn.btn-danger:not(:disabled):not(.disabled):focus {
    box-shadow: 0 0 0 0.2rem rgba(255, 64, 64, 0.5);
}

.footer-container {
    right: 0;
    left: 0;
    bottom: 0;
}
.container-full {
    width: 100%;
    height: 100%;
}

.title-page-container {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: calc(50vh - 60px);
    text-align: center;
    position: relative;
}

.title-page {
    margin: 0px;
    color: rgb(32, 35, 41);
    border: none;
    font-weight: 900;
    z-index: 1;
    font-size: 5.25rem;
}

.container-characters {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-wrap: wrap;
    max-width: 1920px;
}

.container-all-characters {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 4.5rem 0px;
    background: rgb(36, 40, 47);
    min-height: calc(50vh - 60px);
}

@media (max-width: 500px) {
    .title-page {
        font-size: 55px;
    }
}
.character-card-container-images {
    flex: 2 1;
    width: 100%;
}

.character-card-container-images img {
    width: 100%;
    height: 100%;
    margin: 0px;
    opacity: 1;
    transition: opacity 0.5s ease 0s;
    object-position: center center;
    object-fit: cover;
}

.character-card-container-description {
    flex: 3 1;
    position: relative;
    padding: 0.75rem;
    color: rgb(255, 255, 255);
    display: flex;
    flex-direction: column;
    width: 40%;
}

.character-card-container {
    width: min(88%, 600px);
    height: min(35vh, 250px);
    display: flex;
    overflow: hidden;
    background: rgb(60, 62, 68);
    border-radius: 0.5rem;
    margin: 0.75rem;
    box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 6px -1px, rgba(0, 0, 0, 0.06) 0px 2px 4px -1px;
}

.button-tag {
    color: #4165f3;
    transition: color .5s;
}

.button-tag:hover {
    color: #31a10f;
    cursor: pointer
}
.container-modal {
    min-height: min(55vh, 500px);
}
.title-color {
    background-color: rgb(36, 40, 47);
}

.title-episode {
    color: #13508d;
    border: none;
    font-weight: 500;
    z-index: 1;
    font-size: 3.25rem;
}

.carousel {
    overflow: scroll;
    padding-left: 15px;
    width: 100%;
    position: relative;
}

.carousel__container {
    transition: 450ms -webkit-transform;
    transition: 450ms transform;
    transition: 450ms transform, 450ms -webkit-transform;
    font-size: 0;
    white-space: nowrap;
    margin: 20px 0px;
    padding-bottom: 10px;
}

.carousel__container:hover .carousel-item {
    opacity: 0.3;
}

.carousel__container:hover .carousel-item:hover {
    -webkit-transform: scale(1.2);
    transform: scale(1.2);
    opacity: 1;
}

.carousel-item {
    border-radius: 20px;
    overflow: hidden;
    position: relative;
    display: inline-block;
    width: 200px;
    height: 250px;
    margin-right: 15px;
    margin-top: 5px;
    font-size: 20px;
    cursor: pointer;
    transition: 450ms all;
    -webkit-transform-origin: center left;
    transform-origin: center left;
}

.carousel-item:hover~.carousel-item {
    -webkit-transform: translate3d(100px, 0, 0);
    transform: translate3d(100px, 0, 0);
}

.carousel-item__img {
    width: 200px;
    height: 250px;
    object-fit: cover;
}

.carousel-item__details {
    align-items: flex-start;
    background: linear-gradient( to top, rgba(0, 0, 0, 0.9) 0%, rgba(0, 0, 0, 0) 100%);
    bottom: 0;
    display: flex;
    font-size: 10px;
    flex-direction: column;
    justify-content: flex-end;
    left: 0;
    opacity: 0;
    transition: 450ms opacity;
    padding: 10px;
    position: absolute;
    right: 0;
    top: 0;
}

.carousel-item__details--img {
    width: 20px;
}

.carousel-item:hover .carousel-item__details {
    opacity: 1;
}

.carousel-item__details--title {
    color: white;
    margin: 5px 0px 0px 0px;
    max-width: 80%;
}

@media (max-width: 500px) {
    .title-episode {
        text-align: center;
    }
    .carousel-item {
        margin-left: 20%;
    }
}
